/* Grid Lists */
.wp-block-nortic-plugin-grid-list {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 1rem;
}
.cols-1 {
    grid-template-columns: repeat(1, minmax(0, 1fr));
}
.cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
}
.cols-3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
}
.cols-4 {
    grid-template-columns: repeat(4, minmax(0, 1fr));
}
.cols-5 {
    grid-template-columns: repeat(5, minmax(0, 1fr));
}

@media (max-width: 576px) {
    .wp-block-nortic-plugin-grid-list {
        display: block;
        text-align: center;
    }
    .cols-2, .col-3, .col-4, .col-5 {
        grid-template-columns: repeat(1, minmax(0, 1fr));
    }
}

/* Editor Styles */
.wp-block-nortic-plugin-grid-list[data-type="nortic-plugin/grid-list"] {
    display: inherit;
}

.wp-block-nortic-plugin-grid-list[data-type="nortic-plugin/grid-list"] .block-editor-block-list__layout {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 1rem;
}

.wp-block-nortic-plugin-grid-list[data-type="nortic-plugin/grid-list"].cols-1 .block-editor-block-list__layout {
    grid-template-columns: repeat(1, minmax(0, 1fr));
}

.wp-block-nortic-plugin-grid-list[data-type="nortic-plugin/grid-list"].cols-2 .block-editor-block-list__layout {
    grid-template-columns: repeat(2, minmax(0, 1fr));
}

.wp-block-nortic-plugin-grid-list[data-type="nortic-plugin/grid-list"].cols-3 .block-editor-block-list__layout {
    grid-template-columns: repeat(3, minmax(0, 1fr));
}

.wp-block-nortic-plugin-grid-list[data-type="nortic-plugin/grid-list"].cols-4 .block-editor-block-list__layout {
    grid-template-columns: repeat(4, minmax(0, 1fr));
}

.wp-block-nortic-plugin-grid-list[data-type="nortic-plugin/grid-list"].cols-5 .block-editor-block-list__layout {
    grid-template-columns: repeat(5, minmax(0, 1fr));
}

@media (max-width: 576px) {
    .wp-block-nortic-plugin-grid-list[data-type="nortic-plugin/grid-list"].cols-2 .block-editor-block-list__layout,
    .wp-block-nortic-plugin-grid-list[data-type="nortic-plugin/grid-list"].cols-3 .block-editor-block-list__layout,
    .wp-block-nortic-plugin-grid-list[data-type="nortic-plugin/grid-list"].cols-4 .block-editor-block-list__layout, 
    .wp-block-nortic-plugin-grid-list[data-type="nortic-plugin/grid-list"].cols-5 .block-editor-block-list__layout {
        grid-template-columns: repeat(1, minmax(0, 1fr));
    }
}
